.top-products-carousel .swiper-pagination {
    position : absolute;
    top : 20px;
    padding-right: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
}
.swiper-pagination-bullet {
    padding : 1px;
    color : #fff
}
.swiper-pagination-bullet:last-child {
    margin-right: 20px !important;
}
.swiper-cube-shadow {
    background: transparent !important;
}


